import React, { Component } from 'react';
import '../style.css';
import './room.css';
import Nav from '../comment/nav/nav';
import RoomMess from '../comment/roomMess/roomMess';
import OpenRedBag from '../comment/openredbag/openredbag';
import OpenBoaBag from '../comment/openredbag/openboabag';
import Alerts from '../comment/alerts/alerts';
import OpenBag from '../comment/openredbag/openbag';
import Loading from '../comment/loading/loading';
class App extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	openRedBag:[],
    	openBoaBag:[],
    	openAlerts:[],
    	openBag:[],
    	openRedBagNum:0,
    	boaRedBagNum:0,
    	addLoading:[]
			
    };
  }
	redBagClick(type){
		if('openRedBag'==type){//红包点开后的弹窗
			let stop='';
			let _this=this;
			let bagNum=_this.state.openRedBagNum;
			bagNum+=1;
			console.log(bagNum%3==0);
			if(bagNum%3==0){
				let boaNum=bagNum/3;
				_this.setState({boaRedBagNum:boaNum});
				console.log(_this.state.boaRedBagNum+'-===='+boaNum);
			}
			_this.setState({openRedBagNum:bagNum});
			console.log(bagNum);
			clearTimeout(stop);
			stop=setTimeout(function(){
			_this.setState({openRedBag:[<OpenRedBag callBack={_this.cancelBag.bind(_this)}></OpenRedBag>],openBag:[],openBoaBag:[]});
				
			},2000);
		}else if('cancelRedBag'==type){
			this.setState({openRedBag:[]});
		}else if('cancelBoaBag'==type){
			this.setState({openBoaBag:[]});
		}else if('openBoaBag'==type&&this.state.boaRedBagNum>0){//幸运红包打开弹窗
			let boaNum=this.state.boaRedBagNum;
			this.setState({boaRedBagNum:--boaNum});
			this.setState({openBoaBag:[<OpenBoaBag callBack={this.cancelBag.bind(this)}></OpenBoaBag>]});
		}else if('openAlerts'==type){//规则
			this.setState({openAlerts:[<Alerts key='1' callBack={this.cancelRule.bind(this)}></Alerts>]})
		}else if('cancel'==type){
			this.setState({openAlerts:[]});
		}else if('openBag'==type){//红包打开弹窗
			this.setState({openBag:[<OpenBag callBack={this.cancelBag.bind(this)}></OpenBag>]})
		}else if('cancelOpenBag'==type){
			this.setState({openBag:[]})
		}else if("openLoading"==type){
			this.setState({addLoading:[<Loading></Loading>]});
		}else if("cancelLoading"==type){
			this.setState({addLoading:[]})
		}
	}
	componentWillMount(){

		let _this=this;
		
		_this.redBagClick('openLoading');
		setTimeout(function(){
			_this.redBagClick('cancelLoading');
		},1500);
	}
	cancelRule(){
		this.redBagClick('cancel');
	}
	cancelBag(type){
		this.redBagClick(type);	
	}
  render() {
    return (
      <div className="app wrap">
        <Nav></Nav>
        
        <RoomMess callBack={this.redBagClick.bind(this)} boaNum={this.state.boaRedBagNum}></RoomMess>
      	{this.state.openRedBag}
      	{this.state.openBoaBag}
      	{this.state.openAlerts}
      	{this.state.openBag}
      	{this.state.addLoading}
      </div>
    );
  }
}
export default App;
