import React, { Component } from 'react';
import './openredbag.css';
import '../../style.css';
import {openRedBagBg,canleIon,superRedbacket2x} from '../../public';
import GetBagList from './getbaglist';
class OpenRedBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	
    };
    
  }
	cancel(){
		this.props.callBack("cancelRedBag");
	}
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		return(
			<div className="open-wrap">
				<div className="open-box">
					<div className="img-box">
						<img src={openRedBagBg}/>
					</div>
					<div className="open-intro">
						<div className="cancel-box">
							<span onClick={this.cancel.bind(this)} className="glyphicon glyphicon-remove"></span>

						</div>
						<img className="bag-icon" src={superRedbacket2x}/>
						<p className="p1">{lang=="CN"?"超级红包":"Super Hongbao"}</p>
						<p className="all-eos">{lang=="CN"?"你公获得":"You grabbed"}0.4562 eos</p>
					</div>
					
					<GetBagList></GetBagList>
				</div>
			</div>
		)
	}
}

export default OpenRedBag;